import {
	ADD_ITEM,
	INC_QTY,
	REMOVE_ITEM,
	DEC_QTY,
	LOAD_CART,
	LOADING_CART,
} from './types';
import axios from 'axios';

function getTotalPrice(products) {
	if (products) {
		const prices = products.map((product) => product.totalPrice);
		const totalPrice = prices.reduce((total, curr) => total + curr, 0);
		return totalPrice;
	}
	return 0;
}

const authReq = (url, token, type) => {
	if (type === 'del') {
		return axios.delete(url, {
			headers: { authorization: `Bearer ${token}` },
		}); 
	}
	
	return axios.get(url, {
		headers: { authorization: `Bearer ${token}` },
	});
}
	

export const loadCart = (token) => async (dispatch) => {
	if (token) {
		dispatch({ type: LOADING_CART });
		const response = await authReq('/api/v1/ecartproducts/cart', token);
		dispatch({
			type: LOAD_CART,
			payload: {
				cart: response.data.data,
				totalPrice: getTotalPrice(response.data.data),
			},
		});
	}
};

export const addItem = (id,token) => async (dispatch) => {
	dispatch({ type: LOADING_CART });
	const response = await authReq(
		`/api/v1/ecartproducts/product/${id}/addtocart`,
		token
	);
	
	dispatch({
		type: ADD_ITEM,
		payload: {
			cart: response.data.data,
			totalPrice: getTotalPrice(response.data.data),
		},
	});
};

export const removeItem = (id, token) =>async(dispatch) => {
	dispatch({ type: LOADING_CART });
	const response = await authReq(
		`/api/v1/ecartproducts/cart/item/${id}/remove`,
		token,
		'del',
	);
	dispatch({
		type: REMOVE_ITEM,
		payload: {
			cart: response.data.data,
			totalPrice: getTotalPrice(response.data.data),
		},
	});
};

