import {
	ADD_ITEM,
	REMOVE_ITEM,
	INC_QTY,
	DEC_QTY,
	CHECK_PRODUCT_IN_CART,
	LOAD_CART,
	LOADING_CART
} from '../Actions/types';

const initialState = {
	cart: [],
	noOfItems: 0,
	totalPrice: 0,
	inCart: false,
	loading: false,
};

function cartReducer(state = initialState, action) {
	switch (action.type) {
		case LOADING_CART:
			return {
				...state,
				loading: true,
			};

		case LOAD_CART:
			return {
				...state,
				cart: action.payload.cart,
				noOfItems: action.payload.cart.length,
				totalPrice: action.payload.totalPrice,
				loading: false,
			};
		case ADD_ITEM: {
			return {
				...state,
				cart: action.payload.cart,
				noOfItems: action.payload.cart.length,
				totalPrice: action.payload.totalPrice,
				inCart: true,
				loading: false,
			};
		}
		case REMOVE_ITEM: {
			return {
				...state,
				cart: action.payload.cart,
				noOfItems: action.payload.cart.length,
				totalPrice: action.payload.totalPrice,
				loading: false,
			};
		}

		case INC_QTY: {
			return {
				...state,
				cart: action.payload.cart,
				totalPrice: action.payload.totalPrice,
				loading: false,
			};
		}

		case DEC_QTY: {
			return {
				...state,
				cart: action.payload.cart,
				totalPrice: action.payload.totalPrice,
				loading: false,
			};
		}
		case CHECK_PRODUCT_IN_CART: {
			const product = state.cart.find(
				(item) => item.product._id === action.payload
			);
			return {
				...state,
				inCart: product ? true : false,
			};
		}
		default:
			return state;
	}
}

export default cartReducer;
