import { GET_PRODUCT, LOADING_PRODUCT } from '../Actions/types';

const initialState = {
	product: {},
	loading: true,
	reviews: [],
};

function getProductReducer(state = initialState, action) {
	switch (action.type) {
		case GET_PRODUCT:
			return {
				...state,
				product: {...action.payload.product, inCart: action.payload.inCart},
				reviews: action.payload.reviews,
				loading: false,
			};

		case LOADING_PRODUCT:
			return { ...state, loading: true };
		default:
			return state;
	}
}

export default getProductReducer;
