import { GET_PRODUCTS , LOADING_PRODUCTS } from '../Actions/types';

const initialState = {
	products: [],
	totalproducts: 0,
	loading:true,
	category:'',
};

function getProductsReducer(state = initialState, action) {
	
	switch (action.type) {
		case GET_PRODUCTS:
			return {
				...state,
				products: action.payload.products,
				totalproducts: action.payload.products.length,
				category: action.payload.category,
				loading: false,
			};
		case LOADING_PRODUCTS:
			return {
				...state,
				loading:true
			}

		default:
			return state;
	}
}

export default getProductsReducer;
