import {
	USER_AUTH,
	LOADING_USER,
	AUTH_ERROR,
	LOGOUT_USER,
} from '../Actions/types';
import Cookies from 'js-cookie';

const token = () => Cookies.get('jwt');

const initialState = {
	user: null,
	isAuth: token() ? true : false,
	token: token(),
	authError: null,
	loading: false,
};

function authReducer(state = initialState, action) {
	switch (action.type) {
		case USER_AUTH:
			return {
				...state,
				token: token(),
				isAuth: true,
				loading: false,
			};
        
        case LOADING_USER:
			return { ...state, loading: true };
        
        case AUTH_ERROR:
			return {
				...state,
				authError: action.payload,
				loading: false,
			};
        
        case LOGOUT_USER:
			Cookies.remove('jwt');
			return {
				...state,
				token: null,
				isAuth: false,
			};

		default:
			return state;
	}
}

export default authReducer;
